  // runningRef.current = Simulation;

  const generateRandomStuff = () => {
    const rows = [];
    for (let i = 0; i < numRows; i++) {
      rows.push(
        Array.from(Array(numCols), () => (Math.random() > 0.95 ? 1 : 0)) //0.9 => 0.5
      );
    }
    return rows;
  };

  const [grid, setGrid] = useState(() => {
    return generateRandomStuff();
  });

  const runSimulation = useCallback(() => {
    setGrid((g) => {
      return produce(g, (gridCopy) => {
        for (let i = 0; i < numRows; i++) {
          for (let k = 0; k < numCols; k++) {
            let neighbors = 0;
            redundant.forEach(([x, y]) => {
              const newI = i + x;
              const newK = k + y;
              if (newI >= 0 && newK >= 0 && newI < numRows && newK < numCols) {
                neighbors += g[newI][newK];
              }
            });
            if (neighbors < 2 || neighbors > 3) {
              gridCopy[i][k] = 0;
            } else if (g[i][k] === 0 && neighbors === 3) {
              gridCopy[i][k] = 1;
            }
            //drawing!
            if (i >= 4 && i <= 8 && k >= 64 && k <= 80) {
              gridCopy[i][k] = Math.random() > 0.1 ? 1 : 0;
            }
            if (i >= 8 && i <= 12 && k >= 32 && k <= 80) {
              gridCopy[i][k] = Math.random() > 0.1 ? 1 : 0;
            }
            if (i >= 13 && i <= 17 && k >= 42 && k <= 80) {
              gridCopy[i][k] = Math.random() > 0.1 ? 1 : 0;
            }
            if (i >= 18 && i <= 21 && k >= 62 && k <= 80) {
              gridCopy[i][k] = Math.random() > 0.1 ? 1 : 0;
            }
          }
        }
      });
    });
    setTimeout(runSimulation, 1700);
  }, []);

  useEffect(() => {
    runSimulation();
    setTimeout(runSimulation, 10);
  }, [runSimulation]);

